@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600;800&display=swap);
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent}body{line-height:1}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}nav ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}a{margin:0;padding:0;font-size:100%;vertical-align:baseline;background:transparent}ins{background-color:#ff9;color:#000;text-decoration:none}mark{background-color:#ff9;color:#000;font-style:italic;font-weight:bold}del{text-decoration:line-through}abbr[title],dfn[title]{border-bottom:1px dotted;cursor:help}table{border-collapse:collapse;border-spacing:0}hr{display:block;height:1px;border:0;border-top:1px solid #cccccc;margin:1em 0;padding:0}input,select{vertical-align:middle}button{border:none}body{margin:0;padding:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box;font-family:'Raleway'}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{max-width:1120px;margin:0 auto}

.header{background:#422ed4;padding:8px 0}.header__row{display:flex;align-items:center}.header__logo{margin:0 8px 0 0}.header__title{font-weight:800;font-size:16px;line-height:19px;color:#ffffff}

.chuck__btn{display:block;flex:0 0 14%;background:#fff;border-radius:20px;font-size:16px;font-weight:800;line-height:19px;padding:16px;margin:10px 10px;color:#1c1c39;cursor:pointer;box-shadow:0px 100px 80px rgba(80,76,103,0.07),0px 0px 10.0172px rgba(80,76,103,0.035)}.chuck__btn:hover{background:#422ed4}

.categories{padding:60px 0 0;min-height:412px}.categories__title{font-weight:800;font-size:50px;line-height:59px;margin:0 0 60px 0;text-align:center;color:#1c1c39}.categories__row{display:flex;flex-wrap:wrap;justify-content:center;transition:all 0.7s ease}

.quote{background:url(/chuck/static/media/chuck.125e41cb.png) no-repeat right top;padding:153px 0 0 0}.quote__block{background:#422ed4;color:white;padding:24px;font-weight:600;font-size:20px;line-height:150%;box-shadow:0px 100px 80px rgba(80,76,103,0.07),0px 0px 10.0172px rgba(80,76,103,0.035);border-radius:25px;transition:all 0.7s ease}

.wrapper{background:#f8f8f8;width:100%;height:100vh}

